"use client";
import "./App.css";
import React, { useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
// Components

import Home from "./components/Home";
import Dao from "./components/kokoDao/01-kokoWorld";
import MyOrders from "./components/MyOrders";
import About from "./components/About";
function App() {
  const [account, setAccount] = useState(null);
  return (
    <div className="overlay">
      <div className="App">
        <Router basename="/test">
          {/* <Router> */}
          <Routes>
            <Route
              path="/"
              element={<Home account={account} setAccount={setAccount} />}
            />
            {/* 定义根路径 "/" 对应的组件为 Home */}
            <Route
              path="/Dao"
              element={<Dao account={account} setAccount={setAccount} />}
            />
            <Route
              path="/About"
              element={<About account={account} setAccount={setAccount} />}
            />
            <Route
              path="/MyOrders"
              element={<MyOrders account={account} setAccount={setAccount} />}
            />
            About
          </Routes>
        </Router>
      </div>
    </div>
  );
}

export default App;
